@import url(https://fonts.googleapis.com/css2?family=Work+Sans&display=swap);
body {
  margin: 0;
  font-family: 'Work sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.cardinformation{
    display: inline-flex;
}

.card {
    width: 21rem;
    height: 7.75rem;
    border-radius: 0.875rem;
    color: white;
    position: relative;
    margin: 1rem;
}

.card--purple{
    background-image: linear-gradient(243.52deg, #653FBB 0%, #BA67D3 100%);
}

.card--green {
    background-image: linear-gradient(243.52deg, #3599CC -0.75%, #2ED4A6 100%);
}

.card--orange {
    background-image: linear-gradient(243.52deg, #EE7764 0%, #F7BA71 100%);
}

.card__heading {
    font-weight: normal;
    font-size: 2rem;
    position: absolute;
    top: 5rem;
    left: 1rem;
}

.card__totalcase {
    font-weight: bold;
    font-size: 3rem;
    position: absolute;
    top: 0.5rem;
    left: 1rem;
}

.card__newcase {
    font-weight: normal;
    font-size: 2rem;
    position: absolute;
    top: 5rem;
    right: 2rem;
}

.logo {
    width: 3.515625rem;
    height: 3.515625rem;
    position: absolute;
    top: 1rem;
    right: 2rem;
}


